
import Main from './courses'
import {BrowserRouter as Router, Route} from 'react-router-dom'
import Welcome from './welcome';
import React from 'react';


class App extends React.Component {
  

  constructor(props) {
    super(props);
    this.state = {data: null};
  }

  componentDidMount() {
    fetch('/courses/data.json').then(response => {
        response.json().then(data => {
            this.setState({data: data});
        })
    })            
  }

  render(){

    if(!this.state.data)
      return <div>Loading...</div>

    return (
    
      <div className="App" >
        <Router>
            <Route
                path={["/", "/courses"]} 
                exact 
                render={() => <Welcome />}
            />
        </Router>
        <Router>
            <Route
                path={["/corejava", "/courses/corejava"]} 
                exact 
                render={() => <Welcome />}
            />
          </Router>
          
          <Router>
            <Route
                path={["/frontend-intro", "/courses/frontend-intro"]} 
                exact 
                render={() => <Main data = {this.state.data['frontend-intro']} />}
            />
        </Router>

      </div>
    )
  };
}

export default App;
